* {
  box-sizing: border-box;
  margin: 0;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.calculator-container {
  display: flex;
  margin-top: 5rem;
  gap: 10rem;
}

.calculator-container p {
  font-size: 2rem;
  font-family: 'Quantico', cursive;
  color: #603d14;
}

.calculator-div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 1px solid rgb(116, 116, 116);
}

.display-div {
  padding: 1.2rem;
  align-self: stretch;
  background: #8b8888;
  text-align: end;
  font-size: 1.5rem;
  color: #fff;
}

.button-grid {
  display: -ms-grid;
  display: grid;
  grid-template: repeat(5, 1fr) / repeat(4, 1fr);
}

.button-grid button {
  padding: 2rem;
  font-size: 1rem;
  border: 1px solid #e5c7b2;
}

.zero {
  grid-column: 1 / span 2;
}

.orange-background {
  background-color: #f98538;
}

.navbar {
  overflow: hidden;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #2e1309;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.navbar h1 {
  font-family: 'Henny Penny', cursive;
  font-size: 2.2rem;
  color: #bfb8b8;
}

.navbar ul {
  display: flex;
  justify-content: space-between;
  list-style: none;
}

.navbar ul li {
  padding: 1.5rem;
}

.navbar ul li a {
  text-decoration: none;
  font-family: 'Quantico', sans-serif;
  font-size: 1.2rem;
  color: #f5f763;
}

.navbar ul li a:active,
.navbar ul li a:focus {
  border: 2px solid #f5f763;
  padding: 0.7rem;
}

.welcome {
  font-family: 'Quantico', sans-serif;
  font-size: 2rem;
  margin: 1rem;
}

.welcome p {
  color: #603d14;
}

.quote-div {
  font-family: 'Quantico', sans-serif;
  font-size: 2rem;
  padding: 2rem;
}

.quote-div p {
  text-align: center;
  font-weight: 700;
  color: #603d14;
}

@media only screen and (max-width: 600px) {
  * {
    padding: 0;
    margin: 0;
  }

  .navbar {
    flex-direction: column;
    align-items: flex-start;
    z-index: 2;
  }

  .navbar h1 {
    padding-top: 1rem;
    align-self: center;
  }

  .navbar ul {
    align-self: center;
    padding: 0.5rem;
  }

  .welcome {
    font-size: 1.3rem;
    margin: 5rem 1rem 1rem 1rem;
  }

  .calculator-container {
    flex-direction: column;
    margin-top: 10rem;
    gap: 3rem;
  }

  .calculator-container p {
    align-self: center;
  }
}
